import React from 'react'
import moment from 'moment'
import styled, { css, keyframes, createGlobalStyle } from 'styled-components'

import BaseTable, {
  Column,
  SortOrder,
  AutoResizer,
  normalizeColumns,
  unflatten,
} from 'react-base-table'
import 'react-base-table/styles.css'

import { createAction } from 'utils/actionChannel'

const action = createAction(__filename)
const noop = () => {}
const delay = ms => new Promise(resolve => setTimeout(resolve, ms))

const Table = props => <BaseTable width={720} height={400} {...props} />
Table.Column = Column

const generateColumns = (count = 10, prefix = 'column-', props) =>
  new Array(count).fill(0).map((column, columnIndex) => ({
    key: `${prefix}${columnIndex}`,
    dataKey: `${prefix}${columnIndex}`,
    title: `Column ${columnIndex}`,
    width: 150,
    ...props,
  }))

const generateData = (columns, count = 200, prefix = 'row-') =>
  new Array(count).fill(0).map((row, rowIndex) => {
    return columns.reduce(
      (rowData, column, columnIndex) => {
        rowData[column.dataKey] = `Row ${rowIndex} - Col ${columnIndex}`
        return rowData
      },
      {
        id: `${prefix}${rowIndex}`,
        parentId: null,
      }
    )
  })

const columns = generateColumns(10)
const data = generateData(columns, 200)

const fixedColumns = columns.map((column, columnIndex) => {
  let frozen
  if (columnIndex < 2) frozen = Column.FrozenDirection.LEFT
  if (columnIndex > 8) frozen = Column.FrozenDirection.RIGHT
  return { ...column, frozen }
})

const Footer = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  font-size: 16px;
`

export default () => (
  <Table
    fixed
    columns={columns}
    data={data}
    footerHeight={50}
    footerRenderer={<Footer>Custom Footer</Footer>}
  />
)

